.app-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  height: 100vh;
  text-align: center;
  background-color: #22333B;
  overflow-y: auto;
  padding: 5px;
}

.input-container {
  margin: 10px;
  padding: 10px;
  border: 1px solid #22333B;
  border-radius: 10px;
  background-color: #2A3F54;
  width: 32%;
  text-align: center;
  box-sizing: border-box;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s;
}

.input-container:hover {
  transform: scale(1.05);
}

.input-box {
  padding: 10px;
  margin-bottom: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 10px;
  width: 90%;
  text-align: center;
  background-color: white;
  color: #22333B;
}

.input-box::placeholder {
  color: #999;
  opacity: 0.7;
}

.top-input {
  background-color: #0A3D91;
}

.bottom-input {
  background-color: #B22222;
}

.team-data-container {
  text-align: center;
  background-color: #2A3F54;
  border-radius: 10px;
  padding: 10px;
  margin-top: 10px;
  border: 1px solid #4d5255;
}

.section-header {
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 18px;
  color: #E0E0E0;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}

.grid-item {
  font-weight: bold;
  padding: 10px;
  background-color: #1BB8CC;
  font-size: 14px;
  color: #22333B;
  text-align: left;
  border-radius: 5px;
}

#heatmapContainer {
  margin-top: 20px;
  position: relative;
  width: 1000px;
  height: 500px;
}
